<template>
  <div>
    <!-- Drawer New Workflow -->

    <el-drawer
      title="Novo Fluxo de Trabalho"
      :visible.sync="show"
      direction="rtl"
      size="35%"
      :before-close="handleClose"
    >
      <div class="drawer-content">
        <el-form ref="form" :model="form" label-width="120px">
          <el-form-item label="Nome">
            <el-input v-model="form.name"></el-input>
          </el-form-item>

          <el-form-item label="Em que tipos de dados será executado?">
            <el-select v-model="form.view">
              <el-option v-for="item in form.viewOptions" :key="item" :label="item" :value="item"></el-option>
            </el-select>
          </el-form-item>
          <el-button type="primary" @click="newWorkflow()">Novo Fluxo</el-button>
        </el-form>
      </div>
    </el-drawer>
  </div>
</template>

<script>
export default {
  name: "NewWorkflow",
  data() {
    return {
      visible: false,
      form: {
        name: "",
        view: "",
        viewOptions: ["leads", "accounts", "deals", "contacts"]
      }
    };
  },
  props: ["show"],
  methods: {
    handleClose(done) {
      this.$confirm("Você tem certeza que deseja fechar esta janela?")
        .then(d => {
          this.$emit("done");
          done(d);
        })
        .catch({});
    },
    clearAllFields() {
      this.form.name = "";
      this.form.view = "";
    },
    validateFields() {
      if (
        this.form.label == "" ||
        this.form.action == "" ||
        this.form.view == ""
      ) {
        this.$message.error("Você deve preencher todos os campos");
        return false;
      } else {
        return true;
      }
    },
    newWorkflow() {
      var data = {
        label: this.form.name,
        view: this.form.view
      };

      this.$http.post("/workflows", data).then(res => {
        if (res.status == "201") {
          this.$emit("done");
          this.clearAllFields();
          this.visible = false;
          this.$router.push({
            name: "Settings_Custom_Workflows_View",
            params: { id: res.data.id }
          });

          this.$message({
            message: "Fluxo criado com sucesso",
            type: "success"
          });
          this.listWorkflows();
        }
      });
    }
  }
};
</script>

