<template>
  <div id="cb">
    <el-row>
      <el-col :xs="24" :sm="24" :md="20" :lg="20" :xl="20">
        <h1 class="pageTitle">{{ this.pageTitle }}</h1>
      </el-col>
      <el-col :xs="24" :sm="24" :md="4" :lg="4" :xl="4" class="text-right">
        <el-button type="primary" @click="showNewWorkflow()">Novo Fluxo</el-button>
      </el-col>

      <el-col id="listData" :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
        <el-row>
          <el-table :data="tableData" style="width: 100%">
            <el-table-column width="60">
              <template slot-scope="scope">
                <el-switch
                  v-model="tableData[scope.$index].is_active"
                  @change="toggleActive(scope.row.id, tableData[scope.$index].is_active)"
                ></el-switch>
              </template>
            </el-table-column>
            <el-table-column prop="name" label="Nome do Fluxo"></el-table-column>
            <el-table-column prop="type" label="Tipo"></el-table-column>
            <el-table-column width="150">
              <template slot-scope="scope">
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  @click="editWorkflow(scope.row.id)"
                  circle
                ></el-button>
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  @click="deleteWorkflow(scope.row.id)"
                  circle
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-row>
      </el-col>
    </el-row>

    <!-- Form New -->
    <DrawerNewWorkflow :show="visible_new" @done="doneNewWorkflow()" />
  </div>
</template>

<script>
import DrawerNewWorkflow from "@/views/restricted/Settings/Workflows/New";
export default {
  name: "SettingsWorkflows",
  components: { DrawerNewWorkflow },
  data() {
    return {
      visible_new: false,
      pageTitle: "Fluxos de Trabalho",
      tableData: []
    };
  },
  head: {
    title: {
      inner: "Configurações | Fluxos de Trabalho",
      complement: "GuiaSales"
    }
  },
  methods: {
    doneNewWorkflow() {
      this.visible_new = false;
      this.listWorkflows();
    },
    showNewWorkflow() {
      this.visible_new = true;
    },
    toggleActive(id, value) {
      var data = {
        is_active: value
      };
      this.$http
        .post("/workflows/change-active/" + id, data)
        .then(res => {
          if (res.status != 400) {
            this.$message({
              message: "Fluxo alterado com sucesso",
              type: "success"
            });
          }
        })
        .catch(err => {
          this.$message.error(
            "Oops, houve um erro ao alterar o campo ->" + err.response.data
          );
          this.listWorkflows();
        });
    },
    editWorkflow(id) {
      this.$router.push({
        name: "Settings_Custom_Workflows_View",
        params: { id: id }
      });
    },
    deleteWorkflow(id) {
      this.$http
        .delete("/workflows/" + id)
        .then(res => {
          if (res.status == "200") {
            this.$message({
              message: "Fluxo apagado com sucesso",
              type: "success"
            });
            this.listWorkflows();
          }
        })
        .catch(err => {
          this.message.error("Houve um erro" + err.response.data);
        });
    },
    listWorkflows() {
      this.$http.get("/workflows").then(res => {
        if (res.status == "200") {
          this.tableData = res.data;
        }
      });
    }
  },
  mounted() {
    this.listWorkflows();
  }
};
</script>

<style lang="scss">
.drawer-content {
  padding: 0px 20px;
}

.content {
  min-height: 40vh;
  text-align: center;
}

#cb {
  #listData {
    background-color: white;
  }
}
</style>